<template>
<div class="orderWrap">
    <header>
        <router-link to="/confirmorder/payment"><i class="el-icon-arrow-left"></i> </router-link>
        <div class="top_title"><span>订单列表</span></div>
    </header>
    <ul class="order_list_ul">
        <li class="order_list_li" v-for="(o,index) in orderArr" :key="index">
            <img :src="imgurl + o.restaurant_image_url" alt="" class="restaurant_image">
            <section class="order_item_right">
                <section>
                    <div class="order_item_right_header">
                        <section class="order_header">
                            <h4><span class="ellipsis">{{o.restaurant_name}}</span><i class="arrow_right el-icon-arrow-right" @click="goDetail(index)"></i></h4>
                            <p class="order_time">{{o.formatted_created_at}}</p>
                        </section>
                        <p class="order_status">等待支付</p>
                    </div>
                    <section class="order_basket">
                        <p class="order_name ellipsis">{{o.basket.group[0][0].name}} 等商品</p>
                        <p class="order_amount">¥{{o.total_amount}}.00</p>
                    </section>
                </section>
                <div class="order_again">
                    <div class="page">
                        <span class="rem_time" style="color: orange; border-width: 1px; border-style: solid; border-color: orange;">去支付(还剩{{minute}}分{{second}}秒)</span>
                    </div>
                </div>
            </section>
        </li>
    </ul>
    <div class="footer">
        <router-link to="">
            <div>
                <i class="el-icon-eleme" id="eleme"></i>
                <span>外卖</span>
            </div>
        </router-link>
        <router-link to="/search">
            <div>
                <i class="el-icon-stopwatch"></i>
                <span>搜索</span>
            </div>
        </router-link>
        <router-link to="">
            <div>
                <i class="el-icon-notebook-2"></i>
                <span>订单</span>
            </div>
        </router-link>
        <router-link to="/profile">
            <div>
                <i class="el-icon-s-custom"></i>
                <span>我的</span>
            </div>
        </router-link>
    </div>
</div>
</template>

<script>
export default {
  name:"Order",
  data(){
      return{
          userid:'',
          orderArr:[],
          imgurl: "https://elm.cangdu.org/img/",
          auth_time:900,
          minute:15,
          second:"00",
      }
  },
  created(){
      this.userid = localStorage.getItem('userId');
      console.log(this.userid)
      this.axios.get("/bos/v2/users/"+ this.userid +"/orders",{
            params:{
                user_id:this.userid,
                limit:10
            }
        }).then(res=>{
            console.log(res)
            this.orderArr = res;
        }).catch(err=>{});
        let auth_timetimer = setInterval(() => {
                this.auth_time -= 1;
                if (this.auth_time <= 0) {
                    this.isShow2 = true;
                    clearInterval(auth_timetimer);
                }
                this.minute = Math.floor(this.auth_time / 60);
                this.second = this.auth_time % 60;
                if (this.second < 10){
                    this.second = "0"+this.second;
                }
                if (this.minute < 10){
                    this.minute = "0"+this.minute;
                }
            }, 1000);
  },
  methods:{
      goprofile(){
           this.$router.push("/profile")
      },
      gomsite(){
            this.$router.push('/msite')
        },
      gosearch(){
            this.$router.push('/search')
        },
      goDetail(i){
          this.$router.push('/orderdetail');
          localStorage.setItem('thisArr',JSON.stringify(this.orderArr[i]))
      }
  },
}
</script>

<style scoped>
    .orderWrap{
        overflow: hidden;
    }
    header{
        background-color: #3190e8;
        position: fixed;
        z-index: 100;
        left: 0;
        top: 0;
        width: 100%;
        height: 1.95rem;
    }
    a{
        text-decoration: none;
    }
    header>a>i{
        left: .4rem;
        width: .6rem;
        height: 1rem;
        color: #fff;
        line-height: 2.2rem;
        margin-left: .4rem;
    }
    .top_title{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 50%;
        color: #fff;
        text-align: center;
    }
    .top_title>span{
        font-size: .8rem;
        color: #fff;
        text-align: center;
        font-weight: 700;
    }
    .footer{
        background-color: #fff;
        position: fixed;
        z-index: 100;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 1.95rem;
        display: flex;
        box-shadow: 0 -0.02667rem 0.05333rem rgba(0,0,0,.1);
    }
    .footer a{
        display: block;
        flex: 1;
        display: flex;
        text-align: center;
        flex-direction: column;
        align-items: center;
    }
    .footer a div{
        flex: 1;
        display: flex;
        text-align: center;
        flex-direction: column;
        align-items: center;
    }
    .footer a div i{
        font-size: .9rem;
        margin-top: .4rem;
        color: #ccc;
    }
    #eleme{
        color: #3190e8;
    }
    .footer a div span{
        font-size: .45rem;
        color: #666;
    }
.order_list_ul{
    padding-top: 1.95rem;
    padding-bottom: 1.95rem;
}
.order_list_li{
    background-color: #fff;
    display: flex;
    margin-bottom: .5rem;
    padding: .6rem .6rem 0;
}
.restaurant_image {
    width: 2rem;
    height: 2rem;
    margin-right: .4rem;
}
.order_item_right{
    flex: 5;
}
.order_item_right_header{
    border-bottom: .025rem solid #f5f5f5;
    padding-bottom: .3rem;
    display: flex;
    justify-content: space-between;
}
.order_header h4 {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    font-size: .75rem;
    color: #333;
    line-height: 1rem;
    width: 9rem;
}
.arrow_right{
    width: .4rem;
    /* height: .4rem; */
    fill: #ccc;
    margin-right: .2rem;
}
.order_time{
    font-size: .55rem;
    color: #999;
    line-height: .8rem;
}
.order_status{
    font-size: .6rem;
    color: #333;
}
.order_basket{
    display: flex;
    justify-content: space-between;
    line-height: 2rem;
    border-bottom: .025rem solid #f5f5f5;
}
.order_name{
    font-size: .6rem;
    color: #666;
    width: 10rem;
}
.order_amount{
    font-size: .6rem;
    color: #f60;
    font-weight: 700;
}
.order_again{
    text-align: right;
    line-height: 1.6rem;
}
.page{
    display: inline-block;
}
.rem_time{
    font-size: .55rem;
    color: orange;
    padding: .1rem .2rem;
    border-radius: .15rem;
}
</style>